import React from 'react';
import ReactDOM from 'react-dom';
import ApiClient from './js/helpers/ApiClient';
import {Provider} from 'react-redux';
import {Router, browserHistory} from 'react-router';
import {syncHistoryWithStore} from 'react-router-redux';
import withScroll from 'scroll-behavior';
import createStore from './js/redux/createStore';
import routes from './js/routes';
import DevTools from './js/containers/devTools/DevTools'


const client = new ApiClient();
const _browserHistory = withScroll(browserHistory);
const store = createStore(_browserHistory, client, window.__data);
const history = syncHistoryWithStore(_browserHistory, store);


//////////////////////
// Render
//如果不用DevTools,可以去webpack.dev.config设置__DEVTOOLS__
const component = (__DEVTOOLS__ && !window.devToolsExtension) ? (
	<div>
		<Router history={history}>
			{routes}
		</Router>
		<DevTools />
	</div>
) : (
	<Router history={history}>
		{routes}
	</Router>
);


let render = () => {
	ReactDOM.render(
		<Provider store={store} key="provider">
			{component}
		</Provider>
		, document.getElementById('react-view'));
};


if (module.hot) {
	const renderNormally = render;
	const renderException = (error) => {
		const RedBox = require('redbox-react');
		ReactDOM.render(<RedBox error={error}/>, document.getElementById('root'));
	};
	render = () => {
		try {
			renderNormally();
		} catch (error) {
			console.error('error', error);
			renderException(error);
		}
	};
	module.hot.accept('./js/routes/index', () => {
		render();
	});
}


render()


